<template>
    <div class="wrapper-popup" v-bind:class="getClassPopup">
        <div class="rule">
            <h3>Lut chi</h3>
            <p>
                <ul>
                    <li>iu chnh final score v n new game  chi</li>
                    <li>Ngi chi  xc xc v cng dn im tm ,nu mun dng li  cng vo im chnh th n HOLD</li>
                    <li>Nu ngi chi quay trng mt s 1 s reset im tm v 0 v chuyn tip lt chi cho ngi cn li</li>
                    <li>         Cui cng ai t c im bng vi final score trc s thng</li>
                </ul>
                 
   
                
            </p>
            <button 
                v-on:click="confirm"
                class="confirm"> hiu</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'popup-rule',
    props: {
        isOpenPopup: { type: Boolean, default: false }
    },
    data() {
        return {

        }
    },
    methods: {
        confirm() {
            console.log('confirm PopupRule.vue');
            this.$emit('handleConfirm')
        }
    },
    computed: {
        getClassPopup: function() {
            return {
                'open-popup': this.isOpenPopup
            }
        }
    }
}
</script>

<style>
    .wrapper-popup {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.4);
        opacity: 0;
        visibility: hidden;
        transition: all .3s ease;
    }
    .wrapper-popup.open-popup {
        opacity: 1;
        visibility: visible;
    }
    .rule {
        width: 500px;
        padding: 20px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1.2);
        background-color: #fff;
        position: absolute;
        font-family: Arial, Helvetica, sans-serif;
        transition: all .3s ease;
    }
    .rule ul li{
        font-size: 19px;
    }
     .rule p{
         padding: 0 20px;
     }
    .open-popup .rule {
        transform: translate(-50%, -50%) scale(1);
    }
    /* scale -> class */
    .rule h3 {
        margin-bottom: 10px;
    }
    .rule .confirm {
        cursor: pointer;
        margin-top: 20px;
        padding: 8px 15px;
        border: 2px solid #333;
        background-color: #fff;
        transition: all .3s ease;
    }
    .rule .confirm:hover {
        color: #fff;
        background-color: #333;
    }
    /* kt hp CSS3 vi VueJs  xy dng hiu ng - Zoom in - Zoom out */
</style>



